import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import ExpansionPanel, {
  ExpansionPanelSummary,
  ExpansionPanelDetails,
} from 'material-ui/ExpansionPanel';
import Typography from 'material-ui/Typography';
import ExpandMoreIcon from 'material-ui-icons/ExpandMore';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  heading: {
    fontSize: '20px',
    fontWeight: '300',
    color: '#053787',
    margin: '10px'
  }
});

function MyExpansionPanel(props) {
  const { classes, panels} = props;
  console.log(panels)
  return (
    <div className={classes.root}>
        {panels.map(panel => (
            <ExpansionPanel disabled={panel.disabled}>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                <Typography className={classes.heading}>{panel.title}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    {panel.body()}
                </ExpansionPanelDetails>
            </ExpansionPanel>
        ))}
      
    </div>
  );
}

MyExpansionPanel.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MyExpansionPanel);



