import React, { Component } from 'react';
import logo from './EMS.png';
import './App.css';
import Clock from '../../components/clock';
import Novartis from './novartis.png'
import ButtonBases from '../../common/ButtonBases'
import Typography from 'material-ui/Typography';
import { withStyles } from 'material-ui/styles';
import MyExpansionPanel from '../../common/ExpansionPanel'
import PropTypes from 'prop-types';

const styles = theme => ({
  heading: {
    fontSize: '25px',
    fontWeight: '400',
    color: '#053787',
    margin: '10px'
  },
  appTitle: {
    fontSize: '30px',
    color: 'white'
  }
});

class Home extends Component {
  constructor(props) {
        super(props);
        this.state = { 
          endDate: 'June, 21, 2018',
          yihong: 'May, 01, 2019',
          my: 'May, 01, 2029',
          rj: 'September, 6, 2069'
        };
   }
  componentDidMount() {
    document.title = "Novartis";
  }

  render() {
    const { classes } = this.props;
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <Typography className={classes.appTitle}>Novartis Interns Workspace</Typography>
        </header>
        <img src={Novartis} className="Clock-bg" alt="banner"/>
        <Typography className={classes.heading}>IA ending in </Typography>

        <Clock endDate={ this.state.endDate } style={{alignSelf: 'center'}}/>

        <ButtonBases/>
        <Typography className={classes.heading}>Other countdown timers</Typography>
        <div className="panels">
          <MyExpansionPanel panels={this.renderPanels()}/>
        </div>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
      </div>
    );
  }

  renderPanels = () => [
    {
      disabled: false,
      title: "Yi Hong's FCH",
      body: () => <Clock endDate={ this.state.yihong } style={{alignSelf: 'center'}}/>
    },
    {
      disabled: false,
      title: "Last girl (that MY will ever slay) appears",
      body: () => <Clock endDate={ this.state.my } style={{alignSelf: 'center'}}/>
    },
    {
      disabled: false,
      title: "RJ stops Uvuvwevwewevvwe-ing",
      body: () => <Clock endDate={ this.state.rj } style={{alignSelf: 'center'}}/>
    }
  ]
}

Home.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Home);



