import React, { Component } from 'react';
import './clock.css';
import Tooltip from 'material-ui/Tooltip';
import { ProgressBar } from 'react-bootstrap'
import PropTypes from 'prop-types';
import Typography from 'material-ui/Typography';
import { withStyles } from 'material-ui/styles';


const styles = theme => ({
    clockNumber: {
        fontSize: '30px',
        fontWeight: '250',
        color: '#1c0177'
    },
    clockUnit: {
        fontSize: '28px',
        fontWeight: '240'
    }
  });


class Clock extends Component {
     constructor(props) {
     super(props);
     this.state = {
               days: 0,
               hours: 0,
               minutes: 0,
               seconds: 0,
               progress: 0
          };
     }
     componentWillMount() {
          this.getTimeUntil(this.props.endDate);
     }
     componentDidMount() {
          setInterval(() => this.getTimeUntil(this.props.endDate), 1000);
     }  
     leading0(num) {
          return num < 10 ? '0' + num : num;
     }
     getTimeUntil(endDate) {
          const time = Date.parse(endDate) - Date.parse(new Date());
          const totalTime = Date.parse(endDate) - Date.parse('January, 08, 2018');
          if (time < 0) {
               this.setState({ days: 0, hours: 0, minutes: 0, seconds: 0, progress: 100 });
          
          } else {

               const seconds = Math.floor((time/1000)%60);
               const minutes = Math.floor((time/1000/60)%60);
               const hours = Math.floor((time/(1000*60*60))%24);
               const days = Math.floor(time/(1000*60*60*24));
               const progress = Math.round((100 - (time/totalTime) * 100) * 10) / 10
               this.setState({ 
                 days, 
                 hours, 
                 minutes, 
                 seconds,
                 progress
              });
          }



     }
     render() {
         const { classes } = this.props;
          return(
            <div className='clock-container'>
              <ProgressBar style={{positive: 'relative'}} active now={this.state.progress}
              label={`${this.state.progress}%`}
              bsStyle={this.state.progress < 10 ? "danger" : this.state.progress < 48 ? "warning" : "success"}/>
              <Tooltip id="tooltip-top" 
              title={`Date: ${this.props.endDate}`} placement="top">
                <div className="Clock" style={{flexDirection: 'row'}}>
                
                      <Typography className={classes.clockNumber}>
                          {this.leading0(this.state.days)} &nbsp;
                      </Typography>
                      <Typography className={classes.clockUnit}>
                          Days &nbsp;
                      </Typography>
                      <Typography className={classes.clockNumber}>
                          {this.leading0(this.state.hours)} &nbsp;
                      </Typography>
                      <Typography className={classes.clockUnit}>
                          Hours &nbsp;
                      </Typography>
                      <Typography className={classes.clockNumber}>
                          {this.leading0(this.state.minutes)} &nbsp;
                      </Typography>
                      <Typography className={classes.clockUnit}>
                          Minutes &nbsp;
                      </Typography>
                      <Typography className={classes.clockNumber}>
                          {this.leading0(this.state.seconds)} &nbsp;
                      </Typography>
                      <Typography className={classes.clockUnit}>
                          Seconds  &nbsp;
                      </Typography>
                </div>
              </Tooltip>
             </div>
          );
     }
}

Clock.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Clock);


